import {
  Card,
  CardContent,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";

export default function CustomCard(props) {
  return (
    <Card
      sx={{
        minWidth: props.width,
        height: props.height,
        opacity: 0.85,
        maxHeight: 680,
        borderRadius: "1rem",
        backgroundColor: "rgba(255,255,255,.8)",
      }}
    >
      <CardContent>
        <Typography
          variant="h5"
          component="div"
          color="text.secondary"
          gutterBottom
          align="center"
        >
          {props.title}
        </Typography>
        <br />
        <Box>
          <Typography variant="body2">
            {props.content}
            <br />
          </Typography>
        </Box>
      </CardContent>
    </Card>
  );
}
