import React from "react";
import Carousel from "react-material-ui-carousel";
import { Card, Typography } from "@mui/material";
import { Box } from "@mui/system";

import avatar from '../../../images/avatar.JPG'
import classes from './carousel.module.css';
import { content, content2 } from "../../../pages/aboutUs/contents";

export default function Example(props) {
  var items = [
    {
      image:avatar,
      name: "Psikolojik Danışman Büşra Çırak",
    },
    {
      name: "Hakkımda",
      description: content,
    },
    {
      name: 'Psikolojik Danışman Kimdir ?',
      description:content2
    }
  ];

  return (
    <Box my={2} mb={5}>
      <Carousel
      navButtonsAlwaysVisible={false}
      animation="slide"
      duration={300}
      stopAutoPlayOnHover={true}
      interval={6000}
      swipe={true}
    >
      {items.map((item, i) => (
        <Item key={i} item={item} />
      ))}
    </Carousel>
    </Box>
    
  );
}

function Item(props) {
  return (
    <Box className={classes.carousel}>
      <Card
        sx={{
          borderRadius: "1rem",
        }}
        className={classes.card}
      >
        {props.item.image && <img src={props.item.image} alt='info images'></img>}
        <Typography p={1.2} justifyContent="center" display="flex" variant="h6">
          {props.item.name}
        </Typography>
        <Typography p={1.2}>{props.item.description}</Typography>
      </Card>
    </Box>
  );
}
