import { Box, Grid, useMediaQuery } from "@mui/material";
import Navbar from "../../components/layout/navBar/navbar";

import divBg from "../../images/b.jpeg";

import classes from "./about-us.module.css";
import Footer from "../../components/layout/footer/footer";
import MediaCard from "../../components/UI/customCard2/mediaCard";
import Card from "../../components/UI/customCard2/card";
import { content, content1 } from "./contents";

import Carousel from "../../components/UI/carousel/carousel";
import React, { useEffect } from "react";
const AboutUs = () => {
  const matches = useMediaQuery("(min-width:768px)");

    useEffect(() => {
      window.scrollTo(0, 0);
    }, []);

  return (
    <div className={classes.header}>
      <div className={classes.image} />
      {matches ? (
        <>
          <Navbar />
          <Box
            mt={3}
            width="98%"
            marginLeft="1%"
            minHeight="54rem"
            height="auto"
            sx={{
              borderRadius: "2rem",
              backgroundColor: "white",
              justifyContent: "center",
              display: "flex",
              backgroundImage: `url(${divBg})`,
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
              userSelect: "none",
            }}
            mb={5}
          >
            <Grid container spacing={6} justifyContent="center" m={5}>
              <Grid item xs={3.5} sx={{ marginLeft: -5 }}>
                <MediaCard />
              </Grid>
              <Grid item xs={4}>
                <Card
                  width={325}
                  title="Psikolojik Danışman Büşra Çırak"
                  content={content}
                  height="92%"
                />
              </Grid>
              <Grid item xs={4}>
                <Card
                  width={325}
                  title="Psikolojik Danışman Kimdir ?"
                  content={content1}
                  height="92%"
                />
              </Grid>
            </Grid>
          </Box>
        </>
      ) : (
        <>
          <div className={classes.header}>
            <div className={classes.image}>
              <img src={divBg} alt="div background"></img>
            </div>
            <Navbar />
          </div>
          <Carousel />
        </>
      )}
      <Footer />
    </div>
  );
};

export default React.memo(AboutUs);
