import {
  Card,
  CardMedia,
} from "@mui/material";

import avatar from "../../../images/avatar.JPG";

export default function MediaCard(props) {
  return (
    <Card
      sx={{ minWidth: 325, minHeight: 680, maxHeight: 680,borderRadius:'1rem' }}
    >
      <CardMedia
        sx={{ minHeight: 680, maxHeight: 680, minWidth: 325 }}
        image={avatar}
      />
    </Card>
  );
}
